const WebsiteType = () => {
	const [{ websiteType, currentIndex }, dispatch] = useStateValue();

	return (
		<DefaultStep
			content={
				<>
					<H2>{__('What kind of website are you building for?', 'astra-sites')}</H2>
				</>
			}
			controls={
				// <SquareSelect
				// 	selected={websiteType}
				// 	options={SITE_TYPES}
				// 	onChange={(selectedChoice) => {
				// 		dispatch({
				// 			type: 'set',
				// 			websiteType: selectedChoice.id,
				// 			currentIndex: currentIndex + 1,
				// 		})
				// 	}}
				// />
				<AutoSuggestion
					placeholder="Enter Your Website or Business Type..."
					popularSearches={popularSearches}
					options={suggestions}
				/>
			}
			actions={
				<>
					<PreviousStepLink before>{__('Back', 'astra-sites')}</PreviousStepLink>
					<ProgressBar />
					<NextStepLink ml1 after>{__('Next', 'astra-sites')}</NextStepLink>
				</>
			}
			preview={
				<ImagePreview fileName='step-2.png' />
			}
		/>

	)
}

export default WebsiteType;
